import ScrollPane from 'components/ScrollPane.vue'
import ActionHeader from 'components/ActionHeader.vue'

import Keyboard, {
  UP,
  DOWN,
  DEL,
  BACKSPACE
} from '../../mixins/keyboard'
import EntryList from '../../mixins/entry-list'
import { mapState, mapGetters, mapMutations, mapActions } from 'vuex'
import { classify, focusInput } from 'src/util'

export default {
  components: {
    ScrollPane,
    ActionHeader
  },

  filters: {
    formatTime (timestamp) {
      return (new Date(timestamp)).toString().match(/\d\d:\d\d:\d\d/)[0]
    }
  },

  mixins: [
    Keyboard({
      onKeyDown ({ key, modifiers }) {
        switch (modifiers) {
          case 'ctrl':
            if (key === DEL || key === BACKSPACE) {
              this.reset()
              return false
            } else if (key === 'f') {
              focusInput(this.$refs.filterEvents)
              return false
            }
            break
          case '':
            if (key === UP) {
              this.inspect(this.inspectedIndex - 1)
              return false
            } else if (key === DOWN) {
              this.inspect(this.inspectedIndex + 1)
              return false
            } else if (key === 'r') {
              this.toggleRecording()
            }
        }
      }
    }),
    EntryList
  ],

  computed: {
    ...mapState('events', [
      'enabled',
      'events',
      'inspectedIndex'
    ]),

    ...mapGetters('events', [
      'filteredEvents'
    ]),

    filter: {
      get () {
        return this.$store.state.events.filter
      },
      set (filter) {
        this.$store.commit('events/UPDATE_FILTER', filter)
        this.$store.commit('events/INSPECT', -1)
      }
    }
  },

  methods: {
    ...mapMutations('events', {
      reset: 'RESET',
      toggleRecording: 'TOGGLE'
    }),

    ...mapActions('events', [
      'inspect'
    ]),

    displayComponentName (name) {
      return this.$shared.classifyComponents ? classify(name) : name
    }
  }
}



