import { mapState } from 'vuex'

export default {
  data () {
    return {
      split: 50,
      dragging: false
    }
  },

  computed: {
    ...mapState([
      'view'
    ]),

    leftStyles () {
      const obj = {
        [this.view === 'vertical' ? 'width' : 'height']: `${this.boundSplit}%`
      }
      return obj
    },

    rightStyles () {
      const obj = {
        [this.view === 'vertical' ? 'width' : 'height']: `${100 - this.boundSplit}%`
      }
      return obj
    },

    classes () {
      return [
        { dragging: this.dragging },
        this.view
      ]
    },

    boundSplit () {
      const split = this.split
      if (split < 20) {
        return 20
      } else if (split > 80) {
        return 80
      } else {
        return split
      }
    }
  },

  methods: {
    dragStart (e) {
      this.dragging = true
      this.startPosition = this.view === 'vertical' ? e.pageX : e.pageY
      this.startSplit = this.boundSplit
    },

    dragMove (e) {
      if (this.dragging) {
        let position
        let totalSize
        if (this.view === 'vertical') {
          position = e.pageX
          totalSize = this.$el.offsetWidth
        } else {
          position = e.pageY
          totalSize = this.$el.offsetHeight
        }
        const dPosition = position - this.startPosition
        this.split = this.startSplit + ~~(dPosition / totalSize * 100)
      }
    },

    dragEnd () {
      this.dragging = false
    }
  }
}



